.testimonial {
  position: relative;
  background: #fff;
  padding: 120px 0 50px;
  z-index: 1;
  overflow: hidden;
}

.testimonial-map {
  position: absolute;
  width: 70%;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -40%);
}

.testimonial-map img {
  width: 100%;
}

.testimonial-item {
  box-shadow: 0px 58px 114px 0px rgba(0, 0, 0, 0.08);
  border-radius: 40px 5px 5px 5px;
  padding: 40px 30px;
  position: relative;
  background: #fff;
  margin: 0 15px 150px;
}

.testimonial-item::before {
  position: absolute;
  content: '';
  width: 90%;
  height: 30px;
  background: #e4e5e6;
  border-radius: 5px;
  z-index: -1;
  bottom: 0;
  left: 50%;
  transform: translate(-50%, 30%);
  opacity: .5;
}

.testimonial-item .thumb {
  width: 70px;
  height: 70px;
  margin-bottom: 20px;
  position: relative;
  z-index: 1;
}

.testimonial-item .thumb img {
  width: 100%;
  border-radius: 50%;
}

.testimonial-item .thumb::before {
  position: absolute;
  content: '';
  width: 70px;
  height: 70px;
  background: #9579ff;
  border-radius: 50%;
  opacity: .5;
  transform: translate(3px, -10px);
  left: 0;
  top: 0;
}

.testimonial-item .thumb::after {
  position: absolute;
  content: '';
  width: 70px;
  height: 70px;
  background: #27cdcb;
  border-radius: 50%;
  opacity: .5;
  transform: translate(10px, 0);
  left: 0;
  top: 0;
  z-index: -1;
}

.testimonial-item h3 {
  font-size: 19px;
  margin-bottom: 5px;
}

.testimonial-item p.designation {
  font-family: "Nunito", sans-serif;
  font-weight: 700;
  margin-bottom: 20px;
}

.testimonial-item p.opinion {
  margin-bottom: 0;
}

.testimonial-slider .slick-list {
  overflow: visible;
}

.testimonial-slider .slider-item {
  outline: 0;
}

.testimonial-slider .slick-dots {
  bottom: 60px;
}

.testimonial-slider .slick-dots li button:before {
  transition: all 0.5s ease;
}

.testimonial-slider .slick-dots li.slick-active button:before {
  color: #ff8853;
  opacity: 1;
  font-size: 20px;
}

